<template>
<div id="SingleBlog">
<h1>  {{blog.title}}</h1>
  <article>{{blog.body}}</article>
</div>
</template>

<script>
    export default {
        name: "SingleBlog",
      data(){
          return{
            id:this.$route.params.id,
            blog:{}
          }
      },
      created() {
          this.$http.get('http://jsonplaceholder.typicode.com/posts/'+ this.id)
        .then(response=>{this.blog=response.data})
      }
    }
</script>

<style scoped>

</style>



