<template>
  <div id="AddBlog">

    <form v-if="submmited">
      <h2>添加博客</h2>
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>

      <label >博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">

        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">

        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">

        <label>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories">
      </div>

      <label>作者：</label>

      <select v-model="blog.author">
        <option v-for="author in authors">{{author}}</option>
      </select>

      <button @click.prevent="post">添加博客</button>
    </form>
    <hr>
    <div v-if="!submmited">
      <h3>您的博客发布成功</h3>
    </div>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="item in blog.categories">{{item}}</li>
      </ul>
      <p>作者: {{blog.author}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'AddBlog',
    data () {
      return {
        blog:{
          title: '',
          content: '',
          categories: [],
          author: ''
        },
        authors: ['Henry', 'Hemiah', 'Bucky'],
        submmited: true
      }
    },
    methods:{
      post(){
        this.$http.post("https://base-song-default-rtdb.firebaseio.com/posts.json",
          this.blog
          // {
          // title: this.blog.title,
          // content: this.blog.content,
          // categories: this.categories,
          // author: this.author,
        // }
        )
          .then(response=>{this.submmited=false})
            // function (response){
            //   console.log(response);
            //   this.submmited = false},
          // )
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#AddBlog *{
  box-sizing: border-box;
}

#AddBlog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

  label{
    display: block;
    margin: 20px 0 10px;
  }

  input[type='text'],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }

  textarea{
    height: 200px;
  }

  #checkboxes label{
    display: inline-block;
    margin-top: 0;
  }

  #checkboxes input{
    display: inline-block;
    margin-right: 10px;
  }

  button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: white;
    border: 0;
    padding: 14px;
    border-radius: 6px;
    font-size: 14px;
    cursor: pointer;
  }

  #preview{
    padding: 10px 20px;
    border: 1px solid;
    margin: 30px 0;
  }

  h3{
    margin-top: 10px;

  }
</style>



